import { Component, OnInit } from '@angular/core';
import {MessageService} from '../common.service'
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.css']
})
export class PaymentComponent implements OnInit {
  check = "";  
  total = "0"
  cardnum: string="";
  date: string="";
  cvc: string="";
  subscription: Subscription;
  constructor(private messageService: MessageService) {
    this.subscription = this.messageService.getMessage().subscribe(message => {
        this.check = message.text
      });
      this.subscription = this.messageService.getTotal().subscribe(message => {
        this.total = message.text
        console.log(this.total)
      });
  }
  back(){
    this.messageService.sendMessage("item")
  }
  cardUp(event: any) { // without type info
    this.cardnum += event.target.value;
  }
  dateUp(event: any) { // without type info
    this.date += event.target.value;
  }
  cvcUp(event: any) { // without type info
    this.cvc += event.target.value;
  }
  submit(){

    if(this.cardnum==""){
      alert("Card number required")
      return;
    }
    if(this.date==""){
      alert("Date required")
      return;
    }
    if(this.cvc==""){
      alert("CVC number required")
      return;
    }

    this.messageService.payment(this.total)
    .subscribe((data) => {
      if(data['status']="sucess"){
        this.messageService.sendMessage("item")
        alert("Sucess")       
      }else{
        alert("Unable to process")       
      }
    });
 
  }
  
  ngOnInit() {

  }

}
