import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import {MessageService} from '../common.service'
import {Item} from '../Items'
@Component({
  selector: 'app-cartlist',
  templateUrl: './cartlist.component.html',
  styleUrls: ['./cartlist.component.css']
})
export class CartlistComponent implements OnInit {
  items: Item[];
  check = "";
  total ="0"
  subscription: Subscription;
  constructor(private messageService: MessageService) {
    this.subscription = this.messageService.getMessage().subscribe(message => {
        this.check = message.text
      });
  }
  getCartList(): void {
    this.messageService.getCartList()
      .subscribe((data: Item[]) => {
        this.items = data;
        var num =0;
        for(var i =0;i<data.length;i++){
          num = num +parseInt(data[i]['price'].toString());
        }
        this.total = num.toString();
        this.messageService.sendTotal(num.toString());
      });
  }
  removecart(id):void{
    this.messageService.removeCart(id).subscribe(data => {
        if(data['status']=="sucess"){
            alert("Item Removed")
            this.getCartList()
          }else{
          alert("Unable to remove")
        }
      });    
  
  }
  checkout():void{
    if(this.total=="0"){
      alert("Cart is empty")
      return;
    }
    this.messageService.sendMessage("payment")
  }
  ngOnInit() {
      this.getCartList()
  }

}


  